<template>
  <div class="grid xl:grid-cols-4 grid-cols-1 gap-4">
    <div class="col-span-2 grid grid-cols-4 gap-4">
      <div class="col-span-4">
        <Input
          v-model="data.status_name"
          :disabled="disableStatus"
          type="text"
          label="Status Name"
          :view="(validation.status_name.$invalid && 'danger') || 'default'"
          block
        />
        <span
          class="text-red-base mt-2 block"
          v-for="error of validation.status_name.$errors"
          :key="error"
        >
          {{ error.$message }}.
        </span>
      </div>
      <div class="col-span-4">
        <Input
          v-model="data.short_name"
          :disabled="disableStatus"
          type="text"
          label="Short Name"
          :view="(validation.short_name.$invalid && 'danger') || 'default'"
          block
        />
        <span
          class="text-red-base mt-2 block"
          v-for="error of validation.short_name.$errors"
          :key="error"
        >
          {{ error.$message }}.
        </span>
      </div>
      <div class="col-span-4">
        <Input
          v-model="data.description"
          type="textarea"
          label="Description"
          :view="(validation.description.$invalid && 'danger') || 'default'"
          rows="6"
          block
        />
        <span
          class="text-red-base mt-2 block"
          v-for="error of validation.description.$errors"
          :key="error"
        >
          {{ error.$message }}.
        </span>
      </div>
      <template v-if="hasEmailTemplate">
        <div class="col-span-4">
          <EmailTemplateTimeDelay 
            isWorkflow
            :sendingTimes="data.sending_times_json.length"
            :sendingTimesJson="data.sending_times_json"
            @update:sendingTimes="handleUpdateSendingTimes"
            @update:sendingTimesJson="handleUpdateSendingTimesJson"
          />
          <span
            class="text-red-base mt-2 block"
            v-for="error of validation.reminded_days.$errors"
            :key="error"
          >
            {{ error.$message }}.
          </span>
        </div>
        <!-- <div class="col-span-4 col-start-1 my-2">
          <Checkbox
            v-model="data.is_send_email"
            v-if="!isParent"
            :value-off="0"
            :value="1"
            :disabled="disbaleSendMail"
            @input="isSendEmail()"
          >
            Send Email
          </Checkbox>
        </div> -->
      </template>
    </div>
    <div class="col-span-4">
      <div v-show="data.is_send_email == 1 && !isParent && hasEmailTemplate">
        <div class="grid grid-cols-4 gap-4">
          <div class="col-span-4 xl:col-span-2">
            <div class="mb-3">
              <label class="block mb-2">Logo</label>
              <Checkbox
                v-model="data.is_mail_logo_show"
                @input="handleUpdateEmailTheme"
                :loading="loadingTheme"
                type="toggle"
                id="logo"
                :value="1"
                :value-off="0"
              >
                <span class="font-medium">Show company logo</span>
              </Checkbox>
            </div>
            <div class="mb-3">
              <label>BCC</label>
              <MultipleEmailInput
                @email-change="handleBccUpdate"
                :suggestion="false"
                :emails="data.bcc"
              />
            </div>
            <div class="mb-3">
              <Input
                v-model="data.email_subject"
                type="text"
                label="Email Subject"
                :view="
                  (validation.email_subject.$invalid && 'danger') || 'default'
                "
                block
              />
              <span
                class="text-red-base mt-2 block"
                v-for="error of validation.email_subject.$errors"
                :key="error"
              >
                {{ error.$message }}.
              </span>
            </div>
          </div>
          <div v-loading="loadingEditor" class="col-span-4">
            <TextEditor
              v-model="data.email_template"
              ref="refTextEditor"
              :options="ckeditor"
              with-image-upload
            >
              <template #toolbar>
                <Button
                  class="ml-2"
                  @click="handlePreviewEmailTemplate"
                  :loading="loadingPreviewEmail || loadingTheme"
                  :disabled="disableEmailPreview"
                  color="teal"
                  view="smooth"
                  dense
                >
                  <i class="material-icons mr-1">visibility</i>
                  Preview Email
                </Button>
                <Button
                  v-if="resetTemplate"
                  @click.prevent="handleResetTemplate"
                  class="ml-auto"
                  color="orange"
                  view="smooth"
                  dense
                  ><i class="material-icons">restart_alt</i> Reset to Default
                </Button>
              </template>
            </TextEditor>
            <span
              class="text-red-base mt-2 block"
              v-for="error of validation.email_template.$errors"
              :key="error"
            >
              {{ error.$message }}.
            </span>
          </div>
        </div>
      </div>
    </div>
    <div
      v-if="
        shortCodes.length !== 0 && data.is_send_email == 1 && hasEmailTemplate
      "
      class="col-span-4"
    >
      <Table
        class="mt-6"
        v-if="shortCodes && Array.isArray(shortCodes)"
        :data="shortCodes"
      >
        <TableColumn label="Code">
          <template #default="scope">
            <div
              :class="[
                'short-code',
                scope.row.is_disabled ? 'short-code--disabled' : ''
              ]"
            >
              <span class="mr-2">{{ scope.row.code }}</span>
              <i
                v-if="scope.row.is_disabled ? scope.row.disabled : true"
                class="material-symbols-outlined text-lg text-teal-04"
                style="font-variation-settings: 'FILL' 0"
                @click="copyText(scope.row.code)"
              >
                content_copy
              </i>
              <Tooltip v-if="scope.row.tooltip" placement="top">
                <template #content>
                  <div class="w-96">
                    <span v-html="scope.row.tooltip"></span>
                  </div>
                </template>
                <template #default>
                  <i class="material-icons text-lg text-teal-04">info</i>
                </template>
              </Tooltip>
            </div>
          </template>
        </TableColumn>
        <TableColumn label="Description">
          <template #default="scope">
            <div
              :class="[
                'inline-block',
                'short-code',
                scope.row.is_disabled ? 'short-code--disabled' : ''
              ]"
            >
              {{ scope.row.description }}
            </div>
          </template>
        </TableColumn>
      </Table>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, reactive, ref } from 'vue'
import { useRoute } from 'vue-router'
import { useVuelidate } from '@vuelidate/core'
import { helpers, requiredIf } from '@vuelidate/validators'
import { required } from '@/validations/index'
import { ElMessage } from 'element-plus'
import { useUser } from 'composable/user'

import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'
import InputFile from 'components/InputFile.vue'
import Colorpicker from 'components/Colorpicker.vue'
import MultipleEmailInput from 'components/MultipleEmailInput.vue'
import TextEditor from 'components/TextEditor.vue'
import { Editor as ClassicEditor } from '@erdeprsty/ckeditor5-custom-build'
import EmailTemplateTimeDelay from 'components/EmailTemplate/TimeDelay/index.vue'

export default defineComponent({
  name: 'WorkflowForm',
  components: {
    Input,
    InputFile,
    Colorpicker,
    MultipleEmailInput,
    Checkbox,
    Fieldset,
    Button,
    TextEditor,
    EmailTemplateTimeDelay
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        id: null,
        description: null,
        email_subject: null,
        email_template: '',
        reminded_days: 0,
        sending_times_json: [],
        is_send_email: 1,
        short_name: null,
        status_name: null,
        bcc: null,
        color_mail_template: '#000000',
        is_mail_logo_show: 0
      })
    },
    disableStatus: {
      // Open, Intransit, Complete, Not as Describe, Payment Processing, On Hold, dan Being Inspected
      type: Boolean,
      default: false
    },
    disbaleSendMail: {
      type: Boolean,
      default: false
    },
    disableEmailPreview: {
      type: Boolean,
      default: false
    },
    isParent: {
      type: Boolean,
      default: false
    },
    loadingTheme: {
      type: Boolean,
      default: false
    },
    loadingPreviewEmail: {
      type: Boolean,
      default: false
    },
    loadingEditor: {
      type: Boolean,
      default: false
    },
    resetTemplate: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update-email-theme', 'preview-email', 'reset-template'],
  setup(props, { emit }) {
    const refTextEditor = ref(null)
    const data = reactive(props.data)
    const { me } = useUser()
    const route = useRoute()
    const ckeditor = reactive({
      editor: ClassicEditor,
      config: {
        htmlSupport: {
          allow: [
            {
              styles: true,
              classes: true
            }
          ]
        },
        simpleUpload: {
          uploadUrl: `${
            import.meta.env.VITE_BASE_URL_API
          }/v1/pos/settings/email-template/upload-image/${
            route.params.workflowId
          }?buyback_company_token=${me.value.buyback_company_token}`
        }
      }
    })

    const rules = reactive({
      description: { required },
      reminded_days: {
        requiredIf: helpers.withMessage(
          'Remainded days is required',
          requiredIf(() => hasEmailTemplate.value)
        )
      },
      short_name: { required },
      status_name: { required },
      email_subject: {
        requiredIf: helpers.withMessage(
          'Email subject is required',
          requiredIf(
            () =>
              data.is_send_email == 1 &&
              !props.isParent &&
              hasEmailTemplate.value
          )
        )
      },
      email_template: {
        requiredIf: helpers.withMessage(
          'Email body is required',
          requiredIf(
            () =>
              data.is_send_email == 1 &&
              !props.isParent &&
              hasEmailTemplate.value
          )
        )
      }
    })
    const hasEmailTemplate = computed(() => {
      // List workflow names or id that doesn't have email template
      const workflowsWithoutEmailTemplates = [
        'leads',
        1001,
        'not as described',
        5,
        'on hold',
        10,
        'canceled',
        2002,
        'abandoned buyback',
        1000
      ]
      if (data.short_name) {
        return (
          !workflowsWithoutEmailTemplates.includes(data.id) &&
          !workflowsWithoutEmailTemplates.includes(
            data.short_name.toLowerCase()
          )
        )
      } else {
        return !workflowsWithoutEmailTemplates.includes(data.id)
      }
    })
    const shortCodes = computed(() => {
      return (data?.sort_code || []).map((code) => ({
        ...code,
        description: code.description.replace(/\\/g, ''),
        tooltip: code.tooltip ? code.tooltip.replace(/\\n/g, '<br/>') : null
      }))
    })

    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = async () => {
      if (data.is_send_email == 1 && !props.isParent) {
        const isReadyEditor = refTextEditor.value.isEditorReady
        if (!isReadyEditor) return false
        const isSaved = await refTextEditor.value.forceSave()
        if (!isSaved) return false
      }
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const copyText = (text) => {
      navigator.clipboard.writeText(text).then(() => {
        ElMessage({
          type: 'success',
          message: 'Text copied to clipbord'
        })
      })
    }
    const isSendEmail = () => {
      rules.email_subject =
        data.is_send_email == 1 ? { required } : { required: false }
      rules.email_template =
        data.is_send_email == 1 ? { required } : { required: false }
    }
    const handleBccUpdate = (newBccData) => {
      data.bcc = newBccData
    }
    const handleUpdateEmailTheme = () => emit('update-email-theme', true)
    const handlePreviewEmailTemplate = async () => emit('preview-email', true)
    const handleResetTemplate = () => emit('reset-template', true)

    const handleUpdateSendingTimes = (value) => {
      data.reminded_days = value
    }

    const handleUpdateSendingTimesJson = (value) => {
      data.sending_times_json = value
    }

    return {
      refTextEditor,
      data,
      ckeditor,
      hasEmailTemplate,
      shortCodes,
      validation,
      copyText,
      isSendEmail,
      validate,
      handleBccUpdate,
      handleUpdateEmailTheme,
      handlePreviewEmailTemplate,
      handleResetTemplate,
      handleUpdateSendingTimes,
      handleUpdateSendingTimesJson
    }
  }
})
</script>
<style lang="scss">
.ck-editor__editable {
  min-height: 300px;
  max-height: 600px;
  overflow-y: auto;
}

.ck-source-editing-area {
  min-height: 300px;
  max-height: 600px;
  overflow-y: hidden;

  textarea {
    min-height: 300px;
    max-height: 600px;
    overflow-y: auto;
  }
}

.short-code {
  @apply flex gap-2;
  &--disabled {
    @apply text-grey-03;
  }
}
</style>
